*{
font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}
input{
  color: cornflowerblue;
  width: 100%;
  font-size: 1.3rem;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid green;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}